* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

#search-box {
  display: block;
  border: 2px solid rgb(8, 141, 141);
  border-radius: 4px;
  outline: none;
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin: 16px auto;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

#search-box:focus {
  border: 4px solid rgb(12, 192, 192);
  box-shadow: 0px 3px 4px rgb(194, 192, 192), -4px 3px 6px rgb(194, 192, 192),
    4px 3px 6px rgb(194, 192, 192);
}
.country-list {
  width: 332px;
  margin: 0 auto;
}
.card-item {
  padding-left: 16px;
  padding-right: 16px;
  box-shadow: 0px 3px 4px rgb(194, 192, 192), -4px 3px 6px rgb(194, 192, 192),
    4px 3px 6px rgb(194, 192, 192);
}
.card-item {
  display: flex;
  align-items: center;
}
.country-title {
  font-weight: 600;
  font-size: 20px;
}

.country-image {
  margin-right: 16px;
}
.wrapper-card {
  width: 332px;
  margin: 0 auto;
  padding: 16px;

  box-shadow: 0px 8px 10px rgb(194, 192, 192), -10px 8px 15px rgb(194, 192, 192),
    10px 8px 15px rgb(194, 192, 192);
}
.wrapper-card-title {
  font-weight: 700;
  font-size: 20px;
}
.wrapper-card-text {
  font-weight: 600;
  font-size: 18px;
}
